.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 0 20px;
  background-color: #00CCCC;
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.logo-container {
  display: flex;
  align-items: center;
}

.logo-container img {
  height: 50px;
  margin-right: 20px;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex: 1 1;
}

nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  margin: 0 10px;
  cursor: pointer;
}

nav ul li:hover {
  text-decoration: underline;
}

a {
  color: #4682B4;
  text-decoration: none;
}

a:hover {
  color: #4B0082;
  text-decoration: underline;
}
.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 60px;
  padding: 0 20px;
  background-color: #1C1F22;
  color: white;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
}

.footerNav {
  display: flex;
  justify-content: flex-end;
  margin-right: 0;
  margin-left: auto;
  list-style: none;
}

body {
  background-color: #000000;
  padding-top: 80px;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body img {
  display: block;
  margin: auto;
}

